import "./assets/css/panel.css!";
import {AContainer} from "./abstract/a-container";
import {Form} from "./form";
import {Dialog} from "./dialog";
import {Tab} from "./tab";
import {Box} from "./box";
import {ButtonGroup} from "./button-group";
import {EBasicColorStatus} from "../component/enum/e-basic-color-status";
import {IBlockContainer} from "./interface/i-block-container";
import {FullCalendar} from "../widget/full-calendar";
import {IRenderable} from "../core/interface/i-renderable";
import jquery = require("jquery");

export type IPanelItem = Form<any> | Dialog | Tab | Panel | FullCalendar | Box;

/**
 * <img width="100%" src="../../doc_assets/panel_example_1.png">
 * 
 * Container que representa um painel contendo bordas e uma barra de título
 * 
 * @export
 * @memberof module:mvcomponents/container
 * 
 * @param p_subtitle {string} titulo do painel 
 * @example
 *  import {ViewPager, Panel} from "mvcomponents/container";
 *  import {LoginForm} from "./login-form";
 *
 *  let form:LoginForm = new LoginForm();
 *  form.setSize(12);
 *
 *  let panel:Panel = new Panel("Formulário de Login");
 *  panel.append(form);
 *  panel.setSize(4);
 *
 *  let page:ViewPager = new ViewPager();
 *  page.append(panel);
 *  page.appendTo("#conteudo");
 * 
 */
export class Panel extends AContainer<IPanelItem>{
	private title: string;
	public header:ButtonGroup;
	constructor(title: string) {
		super("div", `
			<div class="subcontent_modwindow panel panel-default">
				<div class="panel-heading subtitlemodwindow" style="display:none">
					<span class="panel-heading mv-panel-title"></span>
				</div>
				<div class="conteudo_form panel-body">
				</div>
				<div style="display:none" class="panel-footer block_module_details">
					<span></span>
				</div>
			<div>`
		);

		this.header = new ButtonGroup().addStyleName("panel-header-box pull-right").removeStyleName("col-sm-offset-0 col-sm-12 col-xs-12");                

		jquery(this.element)
			.find(".subtitlemodwindow:first") 
			.append(this.header.element);       

		this.addStyleName("col-xs-12 col-sm-12 col-md-12 col-lg-12 column Panel")
			.setTitle(title);
	}

	/**
	 * @param show {boolean}
	 * @return {Panel}
	 */
	public showTitle(show: boolean): Panel {
		jquery(this.element).find("div.subtitlemodwindow:first")[show ? "show" : "hide"]();
		return this;
	}
	/**
	 * @param p_title {string}
	 * @return {Panel}
	 */
	public setTitle(title: string): Panel {
		this.title = title;
		if (title) {
			jquery(this.element)
				.find("div.subtitlemodwindow:first")
				.show()
				.find(".mv-panel-title:first")
				.text(title);
		} else {
			jquery(this.element)
				.find("div.subtitlemodwindow:first")
				.hide()
				.find(".mv-panel-title:first")
				.text("");
		}
		return this;
	}

	/**
	 * @return {string}
	 */
	public getTitle(): string {
		return this.title || "";
	}

	/**
	 * @param color {EBasicColorStatus}
	 * @return {Panel}
	 */
	public setColor(color: EBasicColorStatus): Panel {
		jquery(this.element).find("div.subcontent_modwindow:first")
			.removeAttr("class")
			.addClass(`subcontent_modwindow panel panel-${EBasicColorStatus[color].toLowerCase()}`);
		return this;
	}

	protected insertItem(item: IPanelItem, method: string): void {
		jquery(this.element).find("div.conteudo_form:first")[method](item.element);
		this.resolveAttachement(item);
	}
}
